//  API  (  )
const API_KEY = process.env.REACT_APP_FOOD_API_KEY;

/**
 *    
 * @param {string} text -  
 * @returns {Array<string>}    
 */
const extractAllergensFromText = (text) => {
  if (!text) return [];

  const normalizedText = text.toLowerCase();

  // 19     ( )
  const allergenKeywords = {
    '': ['', '', '', '', '', 'egg'],
    '': ['', '', '', '', 'milk', '', '', ''],
    '': [''],
    '': ['', '', 'peanut'],
    '': ['', '', '', '', '', 'soy'],
    '': ['', '', '', 'wheat'],
    '': ['', ''],
    '': [''],
    '': ['', ''],
    '': ['', '', '', 'pork'],
    '': [''],
    '': [''],
    '': ['', '', ''],
    '': ['', '', 'walnut'],
    '': ['', '', '', 'chicken'],
    '': ['', '', '', '', 'beef'],
    '': [''],
    '': ['', '', '', '', '', ''],
    '': [''],
  };

  const foundAllergens = new Set();

  //      
  for (const [allergen, keywords] of Object.entries(allergenKeywords)) {
    for (const keyword of keywords) {
      if (normalizedText.includes(keyword)) {
        foundAllergens.add(allergen);
        console.log(`   : "${keyword}" -> ${allergen}`);
        break; //    
      }
    }
  }

  return Array.from(foundAllergens);
};

/**
 *  API   
 * @param {string} barcode -   
 * @returns {Promise<Object>}   
 */
const fetchFromFoodSafetyAPI = async (barcode) => {
  try {
    //  
    let normalizedBarcode = barcode;

    // 14 0    0 
    if (barcode.length === 14 && barcode.startsWith('0')) {
      normalizedBarcode = barcode.substring(1);
      console.log(` : ${barcode} (14) -> ${normalizedBarcode} (13)`);
    }
    // 13 0  880    ( )
    else if (barcode.length === 13 && barcode.startsWith('0') && !barcode.startsWith('088')) {
      normalizedBarcode = barcode.substring(1);
      console.log(`  (13  ): ${barcode} -> ${normalizedBarcode} (12)`);
    }
    // 13  
    else if (barcode.length === 13) {
      normalizedBarcode = barcode;
      console.log(` : ${barcode} (13)`);
    }
    // 12   
    else if (barcode.length === 12 || barcode.length === 8) {
      normalizedBarcode = barcode;
      console.log(` : ${barcode} (${barcode.length})`);
    }
    else {
      console.warn(`  : ${barcode.length} - ${barcode}`);
    }

    console.log(` : ${barcode},  : ${normalizedBarcode}`);

    // XML   (: bar_cd)
    // GitHub Pages        API 
    const url = `https://apis.data.go.kr/1471000/FoodQrInfoService01/getFoodQrAllrgyInfo?serviceKey=${API_KEY}&pageNo=1&numOfRows=100&bar_cd=${normalizedBarcode}`;

    console.log(` API :  ${normalizedBarcode}`);

    const response = await fetch(url);
    const xmlText = await response.text();

    console.log('API  (XML):', xmlText);

    // XML 
    const parser = new DOMParser();
    const xmlDoc = parser.parseFromString(xmlText, 'text/xml');

    //  
    const parseError = xmlDoc.querySelector('parsererror');
    if (parseError) {
      console.error('XML  :', parseError.textContent);
      return null;
    }

    //   
    const resultCode = xmlDoc.querySelector('resultCode')?.textContent;
    const resultMsg = xmlDoc.querySelector('resultMsg')?.textContent;

    console.log(`API  : ${resultCode}, : ${resultMsg}`);

    if (resultCode !== '00') {
      console.warn(`API : ${resultMsg}`);
      return null;
    }

    //   
    const items = xmlDoc.querySelectorAll('item');

    if (items.length === 0) {
      console.log('API    .');
      return null;
    }

    console.log(`${items.length}   .`);

    //     19   
    const allergenMapping = {
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': '',
      '': ''
    };

    const allergenSet = new Set();
    const allergenDetails = [];
    let productName = '';

    items.forEach((item) => {
      const allergenName = item.querySelector('ALG_CSG_MTR_NM')?.textContent || '';
      productName = item.querySelector('PRDCT_NM')?.textContent || '   ';

      if (allergenName) {
        allergenDetails.push(allergenName);

        // 19    
        const mappedAllergen = allergenMapping[allergenName];
        if (mappedAllergen) {
          allergenSet.add(mappedAllergen);
          console.log(`  : ${allergenName} -> ${mappedAllergen}`);
        } else {
          //     
          allergenSet.add(allergenName);
          console.log(`   : ${allergenName}`);
        }
      }
    });

    const allergens = Array.from(allergenSet);

    console.log(`   (${allergens.length}):`, allergens);

    return {
      barcode: barcode,
      name: productName,
      brand: ' ',
      price: ' ',
      ingredients: allergenDetails,
      allergens: allergens,
      warnings: allergens.length > 0
        ? `  ${allergens.join(', ')}()  .`
        : '  .',
      nutrition: {
        calories: ' ',
        sodium: ' ',
        carbs: ' ',
        sugars: ' ',
        fat: ' ',
        protein: ' '
      },
      apiSource: 'foodsafety',
      rawAllergenData: allergenDetails
    };

  } catch (error) {
    console.error(' API  :', error);
    return null;
  }
};

/**
 * HACCP API    (QR API )
 * @param {string} barcode -   
 * @returns {Promise<Object>}   
 */
const fetchFromHACCPAPI = async (barcode) => {
  try {
    //  
    let normalizedBarcode = barcode;

    // 14 0    0 
    if (barcode.length === 14 && barcode.startsWith('0')) {
      normalizedBarcode = barcode.substring(1);
      console.log(`HACCP -  : ${barcode} (14) -> ${normalizedBarcode} (13)`);
    }
    // 13 0  880    ( )
    else if (barcode.length === 13 && barcode.startsWith('0') && !barcode.startsWith('088')) {
      normalizedBarcode = barcode.substring(1);
      console.log(`HACCP -   (13  ): ${barcode} -> ${normalizedBarcode} (12)`);
    }
    // 13  
    else if (barcode.length === 13) {
      normalizedBarcode = barcode;
      console.log(`HACCP -  : ${barcode} (13)`);
    }
    // 12   
    else if (barcode.length === 12 || barcode.length === 8) {
      normalizedBarcode = barcode;
      console.log(`HACCP -  : ${barcode} (${barcode.length})`);
    }
    else {
      console.warn(`HACCP -   : ${barcode.length} - ${barcode}`);
    }

    console.log(`HACCP API -  : ${barcode},  : ${normalizedBarcode}`);

    // HACCP API  ()
    // GitHub Pages        API 
    const url = `https://apis.data.go.kr/B553748/CertImgListService/getCertImgListService?serviceKey=${API_KEY}&pageNo=1&numOfRows=10&bar_cd=${normalizedBarcode}`;

    console.log(`HACCP API :  ${normalizedBarcode}`);

    const response = await fetch(url);
    const xmlText = await response.text();

    console.log('HACCP API  (XML):', xmlText);

    // XML 
    const parser = new DOMParser();
    const xmlDoc = parser.parseFromString(xmlText, 'text/xml');

    //  
    const parseError = xmlDoc.querySelector('parsererror');
    if (parseError) {
      console.error('XML  :', parseError.textContent);
      return null;
    }

    //   
    const resultCode = xmlDoc.querySelector('resultCode')?.textContent;
    const resultMsg = xmlDoc.querySelector('resultMsg')?.textContent;

    console.log(`HACCP API  : ${resultCode}, : ${resultMsg}`);

    if (resultCode !== '00') {
      console.warn(`HACCP API : ${resultMsg}`);
      return null;
    }

    //  
    const items = xmlDoc.querySelectorAll('item');

    if (items.length === 0) {
      console.log('HACCP API    .');
      return null;
    }

    const item = items[0]; //    

    //   
    const productName = item.querySelector('PRDLST_NM')?.textContent ||
                       item.querySelector('PRDCT_NM')?.textContent ||
                       '   ';
    const manufacturer = item.querySelector('BSSH_NM')?.textContent ||
                        item.querySelector('ENTRPS_NM')?.textContent ||
                        ' ';

    //     (API   )
    const allergyFields = [
      item.querySelector('ALLERGY_INFO')?.textContent,
      item.querySelector('ALLRGY_INFO')?.textContent,
      item.querySelector('ALLRGY_PRDLST_INFO')?.textContent,
      item.querySelector('RAWMTRL_NM')?.textContent, // 
      item.querySelector('PRDLST_DCNTS')?.textContent, // 
      item.querySelector('CSTDY_ASORT_MATR')?.textContent, // 
    ];

    //      
    const combinedText = allergyFields.filter(f => f).join(' ');
    console.log('HACCP API  :', combinedText);

    //    
    const allergens = extractAllergensFromText(combinedText);

    console.log(`HACCP API    (${allergens.length}):`, allergens);

    //   
    const rawMaterialText = item.querySelector('RAWMTRL_NM')?.textContent || '';
    const ingredients = rawMaterialText ?
      rawMaterialText.split(/[,]/).map(i => i.trim()).filter(i => i) :
      [];

    return {
      barcode: barcode,
      name: productName,
      brand: manufacturer,
      price: ' ',
      ingredients: ingredients,
      allergens: allergens,
      warnings: allergens.length > 0
        ? `  ${allergens.join(', ')}()    .`
        : '    .    .',
      nutrition: {
        calories: ' ',
        sodium: ' ',
        carbs: ' ',
        sugars: ' ',
        fat: ' ',
        protein: ' '
      },
      apiSource: 'haccp',
      rawAllergenText: combinedText
    };

  } catch (error) {
    console.error('HACCP API  :', error);
    return null;
  }
};

