import React, { useState, useEffect, useRef } from 'react';
import '../styles/AIChatbot.css';

function AIChatbot({ selectedAllergens, onComplete, speak, voiceModeEnabled, setVoiceModeEnabled }) {
  const [messages, setMessages] = useState([]);
  const [inputText, setInputText] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isListening, setIsListening] = useState(false);
  const [conversationState, setConversationState] = useState('intro');
  const [collectedData, setCollectedData] = useState({});
  const [currentAllergenIndex, setCurrentAllergenIndex] = useState(0);
  const messagesEndRef = useRef(null);
  const recognitionRef = useRef(null);

  // LLM API    ( /)
  const API_URL = '/api/v1/chat/completions';
  const USE_LLM = false; //    

  //   (  )
  const getSystemPrompt = () => {
    return `        AI .
        .

### 
1.     .
2.    (2-3 ).
3.    .
4.    .
5.    , /    .

###  
   : ${selectedAllergens.join(', ')}

###   ( )
1.   (, , ,   )
2.   (, 30 , 1-2 )
3.  (, , )
4. (  ) 
5.   (   )
6.   

###   
 ${selectedAllergens[0] || ''}   .

  .   .`;
  };

  // TTS   (     )
  const speakIfEnabled = (text) => {
    if (voiceModeEnabled && speak) {
      speak(text);
    }
  };

  //  
  useEffect(() => {
    const welcomeMessage = {
      role: 'assistant',
      content: `!     AI. \n\n${selectedAllergens.join(', ')}    .\n\n          .  !\n\n "  "      .\n\n? (/ )`
    };
    setMessages([welcomeMessage]);
  }, []);

  //  
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // Web Speech API 
  useEffect(() => {
    if ('webkitSpeechRecognition' in window || 'SpeechRecognition' in window) {
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
      recognitionRef.current = new SpeechRecognition();
      recognitionRef.current.continuous = false;
      recognitionRef.current.interimResults = false;
      recognitionRef.current.lang = 'ko-KR';

      recognitionRef.current.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        setInputText(transcript);
        setIsListening(false);
        speakIfEnabled(`${transcript} .`);
      };

      recognitionRef.current.onerror = (event) => {
        console.error('Speech recognition error:', event.error);
        setIsListening(false);
        speakIfEnabled('  .  .');
      };

      recognitionRef.current.onend = () => {
        setIsListening(false);
      };
    }
  }, []);

  //   
  const startListening = () => {
    if (recognitionRef.current && !isListening) {
      setIsListening(true);
      recognitionRef.current.start();
      speakIfEnabled('.');
    }
  };

  //   
  const stopListening = () => {
    if (recognitionRef.current && isListening) {
      recognitionRef.current.stop();
      setIsListening(false);
    }
  };

  // AI  
  const sendMessageToAI = async (userMessage) => {
    const conversationHistory = [
      ...messages.map(msg => ({
        role: msg.role,
        content: msg.content
      })),
      { role: 'user', content: userMessage }
    ];

    try {
      const response = await fetch(API_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          model: 'gemma-3-12b-it',
          messages: conversationHistory,
          system_prompt: getSystemPrompt(),
          temperature: 0.7,
          max_tokens: 500
        })
      });

      if (!response.ok) {
        throw new Error(`API error: ${response.status}`);
      }

      const data = await response.json();
      const aiResponse = data.choices[0].message.content;

      return aiResponse;
    } catch (error) {
      console.error('AI API Error:', error);

      // :     
      return getFallbackResponse(userMessage);
    }
  };

  //   (API  )
  const getFallbackResponse = (userMessage) => {
    const lowerMessage = userMessage.toLowerCase();

    //  
    if (conversationState === 'intro') {
      if (lowerMessage.includes('') || lowerMessage.includes('') || lowerMessage.includes('yes') || lowerMessage.includes('')) {
        setConversationState('symptoms');
        const allergen = selectedAllergens[currentAllergenIndex];
        return `!  "${allergen}"   .\n\n"${allergen}"()      ?\n\n1.   (, )\n2.   (, )\n3.   (, , )\n4. / \n5. /\n\n    .`;
      } else if (lowerMessage.includes('') || lowerMessage.includes('no')) {
        return '.  ""  "" .';
      } else {
        //    
        return '  ? ""  "" .';
      }
    }

    //  
    if (conversationState === 'symptoms') {
      setConversationState('severity');
      const allergen = selectedAllergens[currentAllergenIndex];
      return ` . "${allergen}"    ?\n\n1  -   ( )\n2  -   (  )\n3  -     \n\n .`;
    }

    //  
    if (conversationState === 'severity') {
      setConversationState('anaphylaxis');
      return `.     ()   ?  ()   ?\n\n   .`;
    }

    //  
    if (conversationState === 'anaphylaxis') {
      setConversationState('cross_reactivity');
      const allergen = selectedAllergens[currentAllergenIndex];

      let crossReactivityQuestion = '';
      if (allergen === '' || allergen === '') {
        crossReactivityQuestion = '     (, , )   .';
      } else if (allergen === '') {
        crossReactivityQuestion = '    , ,    .';
      } else if (allergen === '' || allergen === '') {
        crossReactivityQuestion = '    ,       .';
      } else {
        crossReactivityQuestion = `${allergen}      ?`;
      }

      return `. ${crossReactivityQuestion}\n\n    ?  ?`;
    }

    //  
    if (conversationState === 'cross_reactivity') {
      setConversationState('trace_sensitivity');
      return `.  .\n\n"  ${selectedAllergens[currentAllergenIndex]}   "       ?\n\n,   ?`;
    }

    //  
    if (conversationState === 'trace_sensitivity') {
      const currentAllergen = selectedAllergens[currentAllergenIndex];

      //   
      if (currentAllergenIndex < selectedAllergens.length - 1) {
        const nextIndex = currentAllergenIndex + 1;
        const nextAllergen = selectedAllergens[nextIndex];

        //   (   )
        setCurrentAllergenIndex(nextIndex);
        setConversationState('symptoms');

        return ` "${currentAllergen}"    !\n\n "${nextAllergen}"  .\n\n"${nextAllergen}"()      ?\n\n1.   (, )\n2.   (, )\n3.   (, , )\n4. / \n5. /\n\n    .`;
      } else {
        setConversationState('complete');
        return ` "${currentAllergen}"   !\n\n     !\n\n  :\n${selectedAllergens.map(a => ` ${a}`).join('\n')}\n\n    .\n     .\n\n " "  .`;
      }
    }

    return '.    ?';
  };

  //   (  )
  const handleSendMessage = async () => {
    if (!inputText.trim() || isLoading) return;

    const userMessage = {
      role: 'user',
      content: inputText
    };

    setMessages(prev => [...prev, userMessage]);
    const userInput = inputText;
    setInputText('');
    setIsLoading(true);

    //     
    if (userInput.includes('  ') || userInput.includes(' ')) {
      setVoiceModeEnabled(true);
      const voiceActivationMessage = {
        role: 'assistant',
        content: '   !\n\n    .  ?'
      };
      setMessages(prev => [...prev, voiceActivationMessage]);
      //        (  speak     )
      if ('speechSynthesis' in window) {
        window.speechSynthesis.cancel();
        const utterance = new SpeechSynthesisUtterance('  .     .  ?');
        utterance.lang = 'ko-KR';
        utterance.rate = 0.9;
        utterance.pitch = 1.0;
        window.speechSynthesis.speak(utterance);
      }
      setIsLoading(false);
      return;
    }

    //    (getFallbackResponse    )
    const currentState = conversationState;
    const currentIndex = currentAllergenIndex;

    //      (   )
    const aiResponse = getFallbackResponse(userInput);

    //      
    saveUserResponse(userInput, currentState, currentIndex);

    const assistantMessage = {
      role: 'assistant',
      content: aiResponse
    };

    setMessages(prev => [...prev, assistantMessage]);
    speakIfEnabled(aiResponse);

    //  : "    " 
    if (aiResponse.includes('    ') || aiResponse.includes('   ')) {
      setConversationState('complete');
    }

    setIsLoading(false);
  };

  //     (   )
  const saveUserResponse = (response, state, index) => {
    const allergen = selectedAllergens[index];

    setCollectedData(prevData => {
      const newData = { ...prevData };

      if (!newData[allergen]) {
        newData[allergen] = {};
      }

      switch (state) {
        case 'symptoms':
          newData[allergen].symptoms = response;
          break;
        case 'severity':
          newData[allergen].severity = response;
          break;
        case 'anaphylaxis':
          newData[allergen].anaphylaxis = response;
          break;
        case 'cross_reactivity':
          newData[allergen].crossReactivity = response;
          break;
        case 'trace_sensitivity':
          newData[allergen].traceSensitivity = response;

          //        
          if (index < selectedAllergens.length - 1) {
            console.log(` [${allergen}]  - : ${selectedAllergens[index + 1]}`);
          } else {
            console.log(` [${allergen}]  -    `);
          }
          break;
        default:
          break;
      }

      console.log(`[${allergen}] ${state}: ${response}`);
      return newData;
    });
  };

  //  
  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  //   
  const quickResponses = {
    intro: ['', '', '  '],
    symptoms: ['1', '2', '3', '4', '5', '1, 2', '2, 3'],
    severity: ['1 ()', '2 ()', '3 ()'],
    anaphylaxis: ['', ''],
    cross_reactivity: ['', '', ''],
    trace_sensitivity: ['', '']
  };

  const handleQuickResponse = (response) => {
    setInputText(response);
  };

  const handleComplete = () => {
    //    
    const profile = {
      allergens: selectedAllergens,
      detailedData: collectedData, //   
      conversationHistory: messages,
      timestamp: new Date().toISOString()
    };

    console.log('  :', profile);
    onComplete(profile);
  };

  return (
    <div className="ai-chatbot">
      <div className="chatbot-header">
        <div className="chatbot-title">
          <span className="chatbot-icon"></span>
          <h2>AI  </h2>
        </div>
        <div className="chatbot-status">
          <span className={`voice-mode-indicator ${voiceModeEnabled ? 'enabled' : 'disabled'}`}>
            {voiceModeEnabled ? '   ON' : '   OFF'}
          </span>
          {isListening && (
            <span className="listening-indicator">
                ...
            </span>
          )}
          {isLoading && (
            <span className="loading-indicator">
               AI  ...
            </span>
          )}
        </div>
      </div>

      <div className="chatbot-messages">
        {messages.map((message, index) => (
          <div
            key={index}
            className={`message ${message.role === 'user' ? 'user-message' : 'assistant-message'}`}
          >
            <div className="message-avatar">
              {message.role === 'user' ? '' : ''}
            </div>
            <div className="message-content">
              <div className="message-text">
                {message.content.split('\n').map((line, i) => (
                  <p key={i}>{line}</p>
                ))}
              </div>
            </div>
          </div>
        ))}
        {isLoading && (
          <div className="message assistant-message">
            <div className="message-avatar"></div>
            <div className="message-content">
              <div className="typing-indicator">
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>
          </div>
        )}
        <div ref={messagesEndRef} />
      </div>

      {quickResponses[conversationState] && (
        <div className="quick-responses">
          <p className="quick-responses-label"> :</p>
          <div className="quick-response-buttons">
            {quickResponses[conversationState].map((response, index) => (
              <button
                key={index}
                className="quick-response-btn"
                onClick={() => handleQuickResponse(response)}
                disabled={isLoading}
              >
                {response}
              </button>
            ))}
          </div>
        </div>
      )}

      <div className="chatbot-input-area">
        <div className="input-group">
          <button
            className={`mic-btn ${isListening ? 'listening' : ''}`}
            onClick={isListening ? stopListening : startListening}
            disabled={isLoading}
            aria-label={isListening ? '  ' : '  '}
            title=" "
          >
            {isListening ? '' : ''}
          </button>

          <input
            type="text"
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder=" ... (   )"
            disabled={isLoading}
            aria-label=" "
          />

          <button
            className="send-btn"
            onClick={handleSendMessage}
            disabled={!inputText.trim() || isLoading}
            aria-label=" "
          >
            
          </button>
        </div>
      </div>

      {conversationState === 'complete' && (
        <div className="completion-banner">
          <p>  !</p>
          <button
            className="complete-btn"
            onClick={handleComplete}
            aria-label="  "
          >
             
          </button>
        </div>
      )}
    </div>
  );
}

export default AIChatbot;
