import React, { useState, useRef, useEffect } from 'react';
import { Html5Qrcode } from 'html5-qrcode';
import Quagga from 'quagga';
import '../styles/BarcodeScanner.css';

function BarcodeScanner({ onBarcodeScanned, speak }) {
  const [manualBarcode, setManualBarcode] = useState('');
  const [isScanning, setIsScanning] = useState(false);
  const [lastScannedCode, setLastScannedCode] = useState('');
  const [uploadedImage, setUploadedImage] = useState(null);
  const [isProcessingImage, setIsProcessingImage] = useState(false);
  const html5QrcodeRef = useRef(null);
  const quaggaInitializedRef = useRef(false);
  const lastScanTimeRef = useRef(0);
  const fileInputRef = useRef(null);
  const barcodeFoundRef = useRef(false);

  //      
  const validateAndCleanBarcode = (rawBarcode) => {
    console.log('   :', rawBarcode);

    // 1.  
    const numericOnly = rawBarcode.replace(/\D/g, '');
    console.log('  :', numericOnly);

    // 2.  
    if (numericOnly.length < 8) {
      console.warn('   :', numericOnly.length, '');
      return null;
    }

    // 3. 14  13  ( 0 )
    if (numericOnly.length === 14 && numericOnly.startsWith('0')) {
      const normalized = numericOnly.substring(1);
      console.log(' 14 -> 13 :', numericOnly, '->', normalized);
      return normalized;
    }

    // 4.    
    // EAN-13: 13,   880 
    // EAN-8: 8
    // UPC-A: 12

    if (numericOnly.length === 13) {
      if (numericOnly.startsWith('880')) {
        console.log('  EAN-13 :', numericOnly);
        return numericOnly;
      } else {
        console.log('  EAN-13 :', numericOnly);
        return numericOnly; //   
      }
    }

    if (numericOnly.length === 8) {
      console.log(' EAN-8 :', numericOnly);
      return numericOnly;
    }

    if (numericOnly.length === 12) {
      console.log(' UPC-A :', numericOnly);
      return numericOnly;
    }

    // 5.    
    if (numericOnly.length > 14) {
      // 14    
      const extracted14 = numericOnly.substring(0, 14);
      if (extracted14.startsWith('0')) {
        const normalized = extracted14.substring(1);
        console.log(' 14   :', normalized);
        return normalized;
      }

      // 13  
      const extracted13 = numericOnly.substring(0, 13);
      if (extracted13.startsWith('880')) {
        console.log(' 13 :', extracted13);
        return extracted13;
      }

      //  13  
      const extracted13End = numericOnly.substring(numericOnly.length - 13);
      if (extracted13End.startsWith('880')) {
        console.log('  13 :', extracted13End);
        return extracted13End;
      }
    }

    console.warn('  ,  :', numericOnly);
    return numericOnly;
  };

  //   (      )
  const handleBarcodeDetected = (rawBarcode, source) => {
    //    
    if (barcodeFoundRef.current) {
      return;
    }

    const now = Date.now();

    //    (500ms  )
    if (now - lastScanTimeRef.current < 500) {
      return;
    }

    console.log(` [${source}] Raw  :`, rawBarcode);

    //    
    const validatedBarcode = validateAndCleanBarcode(rawBarcode);

    if (!validatedBarcode) {
      console.warn(` [${source}]   , `);
      return;
    }

    //   
    if (validatedBarcode === lastScannedCode) {
      console.log(` [${source}]   `);
      return;
    }

    //    
    barcodeFoundRef.current = true;
    lastScanTimeRef.current = now;
    setLastScannedCode(validatedBarcode);

    console.log(` [${source}]  :`, validatedBarcode);
    speak(` ${validatedBarcode} .`);
    onBarcodeScanned(validatedBarcode);

    //   
    stopAllScanners();
  };

  //   
  const stopAllScanners = () => {
    // Html5Qrcode 
    if (html5QrcodeRef.current) {
      html5QrcodeRef.current.stop().catch(err =>
        console.log('Html5Qrcode stop error:', err)
      );
    }

    // Quagga 
    if (quaggaInitializedRef.current) {
      Quagga.stop();
      quaggaInitializedRef.current = false;
    }
  };

  useEffect(() => {
    speak('  .      ,  .');

    const startScanner = async () => {
      try {
        barcodeFoundRef.current = false;

        //  Scanner 1: Html5Qrcode 
        html5QrcodeRef.current = new Html5Qrcode('barcode-reader');

        const devices = await Html5Qrcode.getCameras();
        console.log('   :', devices);

        if (devices && devices.length > 0) {
          const backCamera = devices.find(device =>
            device.label.toLowerCase().includes('back') ||
            device.label.toLowerCase().includes('rear') ||
            device.label.toLowerCase().includes('') ||
            device.label.toLowerCase().includes('')
          ) || devices[devices.length - 1];

          console.log(' []    ...');
          console.log(' [Scanner 1] Html5Qrcode ');

          // Html5Qrcode  (QR    )
          await html5QrcodeRef.current.start(
            backCamera.id,
            {
              fps: 10,
              qrbox: { width: 350, height: 120 }, //   
              aspectRatio: 2.9,
              disableFlip: false,
              videoConstraints: {
                facingMode: 'environment',
                width: { ideal: 1920 },
                height: { ideal: 1080 }
              }
            },
            (decodedText, decodedResult) => {
              handleBarcodeDetected(decodedText, 'Html5Qrcode');
            },
            (errorMessage) => {
              //   
            }
          );

          //  Scanner 2: Quagga  (  )
          console.log(' [Scanner 2] Quagga ');

          //    Quagga  (  )
          setTimeout(() => {
            Quagga.init({
              inputStream: {
                name: 'Live',
                type: 'LiveStream',
                target: document.querySelector('#barcode-reader video'), // Html5Qrcode   
                constraints: {
                  facingMode: 'environment',
                  width: { ideal: 1920 },
                  height: { ideal: 1080 }
                }
              },
              decoder: {
                // :    
                readers: [
                  'ean_reader',        // EAN-13 ( )
                  'ean_8_reader',      // EAN-8
                  'code_128_reader',   // Code 128
                  'code_39_reader',    // Code 39
                  'upc_reader',        // UPC-A
                  'upc_e_reader'       // UPC-E
                ],
                multiple: false
              },
              locator: {
                patchSize: 'medium',
                halfSample: true
              },
              numOfWorkers: navigator.hardwareConcurrency || 4, // CPU  
              frequency: 10 //  10 
            }, (err) => {
              if (err) {
                console.warn(' [Scanner 2] Quagga  :', err);
                return;
              }

              console.log(' [Scanner 2] Quagga  ');
              quaggaInitializedRef.current = true;

              // Quagga   
              Quagga.onDetected((result) => {
                if (result && result.codeResult && result.codeResult.code) {
                  //   (   )
                  const error = result.codeResult.decodedCodes
                    .filter(code => code.error !== undefined)
                    .reduce((sum, code) => sum + code.error, 0) /
                    result.codeResult.decodedCodes.filter(code => code.error !== undefined).length;

                  if (error < 0.15) { //  15%  
                    console.log(` [Scanner 2] : ${((1 - error) * 100).toFixed(1)}%`);
                    handleBarcodeDetected(result.codeResult.code, 'Quagga');
                  } else {
                    console.log(` [Scanner 2]  : ${((1 - error) * 100).toFixed(1)}%, `);
                  }
                }
              });

              Quagga.start();
            });
          }, 500); // Html5Qrcode   0.5 

          setIsScanning(true);
        } else {
          console.error('   .');
          speak('   .   .');
        }
      } catch (error) {
        console.error('Camera start error:', error);
        speak('  .   .');
      }
    };

    startScanner();

    //      
    return () => {
      stopAllScanners();
    };
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const handleManualSubmit = (e) => {
    e.preventDefault();
    if (manualBarcode.trim()) {
      speak(` ${manualBarcode} .`);
      onBarcodeScanned(manualBarcode.trim());
      setManualBarcode('');
    }
  };

  const handleTestScan = (barcode, productName) => {
    speak(`${productName}  .`);
    onBarcodeScanned(barcode);
  };

  //    (  +    )
  const detectBarcodeRegions = (canvas, width, height) => {
    const ctx = canvas.getContext('2d');
    const imageData = ctx.getImageData(0, 0, width, height);
    const data = imageData.data;

    const barcodeRegions = [];
    const scanStep = 10; //   10 

    console.log('    ...');

    //      
    for (let y = 0; y < height; y += scanStep) {
      let transitions = 0; //   
      let lastBrightness = 0;
      let regionStart = -1;

      for (let x = 0; x < width; x++) {
        const idx = (y * width + x) * 4;
        const brightness = (data[idx] + data[idx + 1] + data[idx + 2]) / 3;

        //    ( ) 
        if (Math.abs(brightness - lastBrightness) > 50) {
          transitions++;
          if (regionStart === -1) regionStart = x;
        }

        lastBrightness = brightness;
      }

      //   30    
      if (transitions > 30) {
        barcodeRegions.push({
          y: y,
          transitions: transitions
        });
      }
    }

    if (barcodeRegions.length === 0) {
      console.log('    ');
      return null;
    }

    //       ( )
    barcodeRegions.sort((a, b) => b.transitions - a.transitions);
    const centerY = barcodeRegions[0].y;

    //    (  10-20% )
    const estimatedHeight = Math.min(Math.floor(height * 0.2), 200);
    const startY = Math.max(0, centerY - estimatedHeight / 2);
    const endY = Math.min(height, centerY + estimatedHeight / 2);

    // X    (   )
    let leftBound = 0;
    let rightBound = width;

    //      / 
    const midY = Math.floor((startY + endY) / 2);
    let foundStart = false;

    for (let x = 0; x < width; x++) {
      const idx = (midY * width + x) * 4;
      const brightness = (data[idx] + data[idx + 1] + data[idx + 2]) / 3;

      if (!foundStart && brightness < 200) {
        leftBound = Math.max(0, x - 20); //  
        foundStart = true;
      }
    }

    for (let x = width - 1; x >= 0; x--) {
      const idx = (midY * width + x) * 4;
      const brightness = (data[idx] + data[idx + 1] + data[idx + 2]) / 3;

      if (brightness < 200) {
        rightBound = Math.min(width, x + 20); //  
        break;
      }
    }

    const barcodeWidth = rightBound - leftBound;
    const barcodeHeight = endY - startY;

    //  :   
    if (barcodeWidth < 50 || barcodeHeight < 20) {
      console.log(`    : w=${barcodeWidth}, h=${barcodeHeight}`);
      return null;
    }

    //  :   (   )
    const aspectRatio = barcodeWidth / barcodeHeight;
    if (aspectRatio < 1.5 || aspectRatio > 10) {
      console.log(`   : ${aspectRatio.toFixed(2)}`);
      return null;
    }

    console.log(`   : x=${leftBound}, y=${startY}, w=${barcodeWidth}, h=${barcodeHeight}`);
    console.log(`  : ${barcodeRegions[0].transitions}, : ${aspectRatio.toFixed(2)}`);

    return {
      x: leftBound,
      y: startY,
      width: barcodeWidth,
      height: barcodeHeight
    };
  };

  //       
  const createImageRegions = (canvas, width, height) => {
    const regions = [];

    //     
    const barcodeRegion = detectBarcodeRegions(canvas, width, height);

    //       
    if (barcodeRegion && barcodeRegion.width > 0 && barcodeRegion.height > 0) {
      try {
        const barcodeCanvas = document.createElement('canvas');
        barcodeCanvas.width = barcodeRegion.width;
        barcodeCanvas.height = barcodeRegion.height;
        const barcodeCtx = barcodeCanvas.getContext('2d');

        barcodeCtx.drawImage(
          canvas,
          barcodeRegion.x, barcodeRegion.y, barcodeRegion.width, barcodeRegion.height,
          0, 0, barcodeRegion.width, barcodeRegion.height
        );

        regions.push({
          name: 'detected_barcode',
          canvas: barcodeCanvas,
          description: '    ()'
        });

        //     
        const zoomedCanvas = document.createElement('canvas');
        const zoomFactor = 1.5;
        zoomedCanvas.width = Math.floor(barcodeRegion.width * zoomFactor);
        zoomedCanvas.height = Math.floor(barcodeRegion.height * zoomFactor);
        const zoomedCtx = zoomedCanvas.getContext('2d');

        zoomedCtx.drawImage(
          canvas,
          barcodeRegion.x, barcodeRegion.y, barcodeRegion.width, barcodeRegion.height,
          0, 0, zoomedCanvas.width, zoomedCanvas.height
        );

        regions.push({
          name: 'detected_barcode_zoomed',
          canvas: zoomedCanvas,
          description: '    (1.5)'
        });

        console.log('   2  ( + )');
      } catch (err) {
        console.log('     :', err.message);
      }
    }

    //  
    regions.push({
      name: 'full',
      canvas: canvas,
      description: ' '
    });

    // 3x3   
    const gridRows = 3;
    const gridCols = 3;
    const regionWidth = Math.floor(width / gridCols);
    const regionHeight = Math.floor(height / gridRows);

    for (let row = 0; row < gridRows; row++) {
      for (let col = 0; col < gridCols; col++) {
        const x = col * regionWidth;
        const y = row * regionHeight;

        const regionCanvas = document.createElement('canvas');
        regionCanvas.width = regionWidth;
        regionCanvas.height = regionHeight;
        const regionCtx = regionCanvas.getContext('2d');

        //   
        regionCtx.drawImage(
          canvas,
          x, y, regionWidth, regionHeight,
          0, 0, regionWidth, regionHeight
        );

        regions.push({
          name: `grid_${row}_${col}`,
          canvas: regionCanvas,
          description: ` (${row + 1}, ${col + 1})`
        });
      }
    }

    // //   (    )
    const stripHeight = Math.floor(height / 3);
    ['top', 'middle', 'bottom'].forEach((position, idx) => {
      const stripCanvas = document.createElement('canvas');
      stripCanvas.width = width;
      stripCanvas.height = stripHeight;
      const stripCtx = stripCanvas.getContext('2d');

      const y = idx * stripHeight;
      stripCtx.drawImage(
        canvas,
        0, y, width, stripHeight,
        0, 0, width, stripHeight
      );

      regions.push({
        name: `strip_${position}`,
        canvas: stripCanvas,
        description: `${position === 'top' ? '' : position === 'middle' ? '' : ''} `
      });
    });

    //    (50%  )
    const centerWidth = Math.floor(width * 0.5);
    const centerHeight = Math.floor(height * 0.5);
    const centerX = Math.floor(width * 0.25);
    const centerY = Math.floor(height * 0.25);

    const centerCanvas = document.createElement('canvas');
    centerCanvas.width = centerWidth;
    centerCanvas.height = centerHeight;
    const centerCtx = centerCanvas.getContext('2d');

    centerCtx.drawImage(
      canvas,
      centerX, centerY, centerWidth, centerHeight,
      0, 0, centerWidth, centerHeight
    );

    regions.push({
      name: 'center_zoom',
      canvas: centerCanvas,
      description: '  '
    });

    console.log(`  ${regions.length}  `);
    return regions;
  };

  //     
  const preprocessAndScanImage = async (file) => {
    return new Promise((resolve, reject) => {
      const img = new Image();
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');

      img.onload = () => {
        const width = img.width;
        const height = img.height;

        console.log(`   : ${width}x${height}`);

        // Canvas  
        canvas.width = width;
        canvas.height = height;

        //  
        ctx.drawImage(img, 0, 0);

        //     
        const imageData = ctx.getImageData(0, 0, width, height);
        const data = imageData.data;

        //   (1.5)
        const contrast = 1.5;
        const factor = (259 * (contrast + 255)) / (255 * (259 - contrast));

        for (let i = 0; i < data.length; i += 4) {
          data[i] = factor * (data[i] - 128) + 128;     // R
          data[i + 1] = factor * (data[i + 1] - 128) + 128; // G
          data[i + 2] = factor * (data[i + 2] - 128) + 128; // B
        }

        ctx.putImageData(imageData, 0, 0);

        //    
        const regions = createImageRegions(canvas, width, height);

        //    Blob 
        canvas.toBlob((blob) => {
          resolve({
            url: URL.createObjectURL(blob),
            width: width,
            height: height,
            canvas: canvas,
            regions: regions
          });
        }, 'image/jpeg', 0.95);
      };

      img.onerror = () => reject(new Error('  '));
      img.src = URL.createObjectURL(file);
    });
  };

  // Quagga     
  const scanRegionWithQuagga = async (regionCanvas, regionName) => {
    return new Promise((resolve, reject) => {
      // Canvas Blob 
      regionCanvas.toBlob((blob) => {
        if (!blob) {
          reject(new Error('Blob  '));
          return;
        }

        const regionUrl = URL.createObjectURL(blob);

        const config = {
          src: regionUrl,
          numOfWorkers: 0,
          locate: true,
          inputStream: {
            size: Math.max(regionCanvas.width, regionCanvas.height)
          },
          decoder: {
            readers: ['ean_reader', 'ean_8_reader', 'code_128_reader', 'upc_reader'],
            multiple: false
          },
          locator: {
            patchSize: 'medium',
            halfSample: false
          }
        };

        Quagga.decodeSingle(config, (result) => {
          URL.revokeObjectURL(regionUrl);

          if (result && result.codeResult) {
            console.log(` ${regionName}  :`, result.codeResult.code);
            resolve(result.codeResult.code);
          } else {
            reject(new Error(`${regionName}   `));
          }
        });
      }, 'image/jpeg', 0.95);
    });
  };

  //     (Quagga.js )
  const handleImageUpload = async (e) => {
    const file = e.target.files?.[0];
    if (!file) return;

    speak('  .');
    setIsProcessingImage(true);

    const imageUrl = URL.createObjectURL(file);
    setUploadedImage(imageUrl);

    try {
      console.log('  :', file.name, file.type, file.size, 'bytes');

      //     
      console.log('      ...');
      const processed = await preprocessAndScanImage(file);
      console.log('  ,  :', processed.regions.length);

      let result = null;

      //  1:    
      try {
        console.log('  1:    ...');

        for (let i = 0; i < processed.regions.length; i++) {
          const region = processed.regions[i];
          console.log(`  ${i + 1}/${processed.regions.length}: ${region.description}  ...`);

          try {
            result = await scanRegionWithQuagga(region.canvas, region.description);

            if (result) {
              console.log(` ! ${region.description}  :`, result);
              break; //   
            }
          } catch (regionError) {
            console.log(` ${region.description}  ,   ...`);
            //    
          }
        }

        if (!result) {
          throw new Error('    ');
        }

      } catch (quaggaError) {
        console.log('  Quagga  ,   Html5Qrcode ...');

        //  2: Html5Qrcode QR/  
        try {
          const scannerId = 'barcode-reader-file-' + Date.now();
          const scannerDiv = document.createElement('div');
          scannerDiv.id = scannerId;
          scannerDiv.style.display = 'none';
          document.body.appendChild(scannerDiv);

          const html5QrCode = new Html5Qrcode(scannerId);
          result = await html5QrCode.scanFile(file, true);

          // 
          await html5QrCode.clear();
          document.body.removeChild(scannerDiv);

          console.log(' Html5Qrcode  :', result);
        } catch (qrError) {
          console.error(' Html5Qrcode :', qrError);
          throw new Error('   .');
        }
      }

      //    
      const validatedBarcode = validateAndCleanBarcode(result);

      if (!validatedBarcode) {
        speak('   .    .');
        setIsProcessingImage(false);
        setUploadedImage(null);
        URL.revokeObjectURL(imageUrl);
        return;
      }

      console.log('  :', validatedBarcode);
      speak(` ${validatedBarcode} .`);

      //      
      setIsProcessingImage(false);
      onBarcodeScanned(validatedBarcode);

    } catch (error) {
      console.error('   :', error);
      console.error(' :', error.message);

      // 
      setUploadedImage(null);
      URL.revokeObjectURL(imageUrl);
      setIsProcessingImage(false);

      speak('   .    ,    .');

      //    
      alert('   \n\n   .\n\n :\n1.      \n2.     \n3.  " "    \n\n     13 .');
    }
  };

  const handleImageButtonClick = () => {
    speak('  .');
    fileInputRef.current?.click();
  };

  //   
  const sampleProducts = [
    { barcode: '8801019606557', name: '', icon: '' },
    { barcode: '8801062638857', name: '', icon: '' },
    { barcode: '8801047012634', name: '', icon: '' },
    { barcode: '8801007325224', name: ' ', icon: '' },
    { barcode: '8801019312007', name: '', icon: '' },
    { barcode: '8801043001274', name: '', icon: '' }
  ];

  return (
    <div className="barcode-scanner">
      <div className="scanner-card">
        <h2> </h2>

        {/* html5-qrcode    */}
        <div id="barcode-reader" className="scanner-area"></div>

        <div className="scanner-instructions">
          <h3> </h3>
          <ol>
            <li>    </li>
            <li>    </li>
            <li>     </li>
            <li>   </li>
          </ol>
        </div>

        <div className="divider">
          <span></span>
        </div>

        {/*    */}
        <div className="image-upload-section">
          <h3>   </h3>
          <p style={{ fontSize: '0.9em', color: '#666', marginBottom: '10px' }}>
             <strong>:</strong>        
          </p>
          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            onChange={handleImageUpload}
            style={{ display: 'none' }}
            aria-label="  "
          />
          <button
            type="button"
            onClick={handleImageButtonClick}
            className="upload-btn"
            disabled={isProcessingImage}
            aria-label="  "
          >
            {isProcessingImage ? ' ...' : '   '}
          </button>
          {uploadedImage && (
            <div className="uploaded-image-preview">
              <img src={uploadedImage} alt="  " />
              <p style={{ fontSize: '0.85em', color: '#999', marginTop: '5px' }}>
                      
              </p>
            </div>
          )}
        </div>

        <div className="divider">
          <span></span>
        </div>

        <form onSubmit={handleManualSubmit} className="manual-input-form">
          <h3> </h3>
          <div className="manual-input-group">
            <input
              type="text"
              value={manualBarcode}
              onChange={(e) => setManualBarcode(e.target.value)}
              placeholder="   (: 8801234567890)"
              pattern="[0-9]*"
              maxLength="13"
              onFocus={() => speak('  ')}
              aria-label="  "
            />
            <button
              type="submit"
              className="scan-btn"
              disabled={!manualBarcode.trim()}
              aria-label=" "
            >
              
            </button>
          </div>
        </form>

        <div className="test-section">
          <h3>   </h3>
          <p className="test-hint">     </p>
          <div className="sample-products">
            {sampleProducts.map((product) => (
              <button
                key={product.barcode}
                type="button"
                onClick={() => handleTestScan(product.barcode, product.name)}
                className="sample-btn"
                aria-label={`${product.name}  `}
                onFocus={() => speak(`${product.name}  `)}
              >
                <span className="sample-icon">{product.icon}</span>
                <span className="sample-name">{product.name}</span>
              </button>
            ))}
          </div>
        </div>
      </div>

      <div className="tips-card">
        <h3> </h3>
        <ul>
          <li><strong> :</strong>       </li>
          <li><strong>:</strong>     </li>
          <li><strong>:</strong>  10~20cm  </li>
          <li><strong>:</strong>      </li>
        </ul>
      </div>
    </div>
  );
}

export default BarcodeScanner;
