import React, { useState } from 'react';
import Tesseract from 'tesseract.js';
import '../styles/AllergyProfileSetup.css';

const commonAllergens = [
  '', '', '', '', '', '', '', '',
  '', '', '', '', '', '', ''
];

function AllergyProfileSetup({ onComplete, speak }) {
  const [step, setStep] = useState('initial'); // 'initial', 'upload', 'manual'
  const [selectedAllergens, setSelectedAllergens] = useState([]);
  const [customAllergen, setCustomAllergen] = useState('');
  const [severity, setSeverity] = useState('high');
  const [name, setName] = useState('');
  const [diagnosisImage, setDiagnosisImage] = useState(null);
  const [ocrProgress, setOcrProgress] = useState(0);
  const [ocrStatus, setOcrStatus] = useState(''); // 'loading', 'recognizing', 'complete', 'error'
  const [extractedText, setExtractedText] = useState('');

  const toggleAllergen = (allergen) => {
    if (selectedAllergens.includes(allergen)) {
      setSelectedAllergens(selectedAllergens.filter(a => a !== allergen));
      speak(`${allergen} `);
    } else {
      setSelectedAllergens([...selectedAllergens, allergen]);
      speak(`${allergen} `);
    }
  };

  const addCustomAllergen = () => {
    if (customAllergen && !selectedAllergens.includes(customAllergen)) {
      setSelectedAllergens([...selectedAllergens, customAllergen]);
      speak(`${customAllergen} `);
      setCustomAllergen('');
    }
  };

  const handleDiagnosisChoice = (hasDiagnosis) => {
    if (hasDiagnosis) {
      setStep('upload');
      speak('   .    .');
    } else {
      setStep('manual');
      speak('   .    .');
    }
  };

  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      setDiagnosisImage(file);
      speak('  . OCR  .');
      performOCR(file);
    }
  };

  //  Tesseract.js OCR 
  const performOCR = async (file) => {
    try {
      setOcrStatus('loading');
      setOcrProgress(0);
      speak('  .  .');

      const result = await Tesseract.recognize(
        file,
        'kor+eng', //  +  
        {
          logger: (m) => {
            // OCR   
            if (m.status === 'recognizing text') {
              const progress = Math.round(m.progress * 100);
              setOcrProgress(progress);
              setOcrStatus('recognizing');

              //    (25%, 50%, 75%)
              if (progress === 25 || progress === 50 || progress === 75) {
                speak(`  ... ${progress} `);
              }
            }
          }
        }
      );

      const text = result.data.text;
      setExtractedText(text);
      setOcrStatus('complete');
      setOcrProgress(100);

      console.log('OCR  :', text);

      // NLP   
      const extractedData = extractAllergensFromText(text);

      if (extractedData.allergens.length > 0) {
        setSelectedAllergens(extractedData.allergens);
        setSeverity(extractedData.severity);
        speak(`  . ${extractedData.allergens.join(', ')}  .      .`);
      } else {
        speak('    .  .');
      }

      setStep('manual');

    } catch (error) {
      console.error('OCR :', error);
      setOcrStatus('error');
      speak('OCR    .  .');
      setStep('manual');
    }
  };

  //    (NLP)
  const extractAllergensFromText = (text) => {
    const lowerText = text.toLowerCase();
    const foundAllergens = [];
    let detectedSeverity = 'medium';

    //   
    const allergenKeywords = {
      '': ['', '', '', 'milk', '', 'casein'],
      '': ['', '', 'egg', '', ''],
      '': ['', 'peanut', ''],
      '': ['', '', '', '', 'nuts', 'almond', 'walnut'],
      '': ['', '', 'wheat', '', 'gluten'],
      '': ['', '', 'soy', 'soybean'],
      '': ['', '', 'fish'],
      '': ['', '', '', 'shrimp', 'crab', 'lobster'],
      '': ['', '', 'shellfish', 'clam'],
      '': ['', 'buckwheat'],
      '': ['', 'peach'],
      '': ['', 'tomato']
    };

    //   
    for (const [allergen, keywords] of Object.entries(allergenKeywords)) {
      for (const keyword of keywords) {
        if (lowerText.includes(keyword)) {
          if (!foundAllergens.includes(allergen)) {
            foundAllergens.push(allergen);
          }
          break;
        }
      }
    }

    //  
    const severityKeywords = {
      high: ['', 'anaphylaxis', '', 'epipen', '', '', '', ''],
      medium: ['', '', ''],
      low: ['', '', '']
    };

    for (const [level, keywords] of Object.entries(severityKeywords)) {
      for (const keyword of keywords) {
        if (lowerText.includes(keyword)) {
          detectedSeverity = level;
          break;
        }
      }
      if (detectedSeverity === level) break;
    }

    return {
      allergens: foundAllergens,
      severity: detectedSeverity,
      rawText: text
    };
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (selectedAllergens.length === 0) {
      speak('    .');
      return;
    }

    const profile = {
      name: name || '',
      allergens: selectedAllergens,
      severity: severity,
      hasDiagnosis: diagnosisImage !== null,
      createdAt: new Date().toISOString()
    };

    onComplete(profile);
  };

  //   
  if (step === 'initial') {
    return (
      <div className="allergy-profile-setup">
        <div className="setup-card initial-choice">
          <h2>  </h2>
          <p className="setup-description">
                  .
          </p>

          <div className="diagnosis-choice">
            <h3>  ?</h3>
            <p className="choice-hint">
                OCR     .
            </p>

            <div className="choice-buttons">
              <button
                type="button"
                className="choice-btn has-diagnosis"
                onClick={() => handleDiagnosisChoice(true)}
                onFocus={() => speak('  ')}
                aria-label=" "
              >
                <span className="choice-icon"></span>
                <span className="choice-text">,  </span>
                <span className="choice-desc">  </span>
              </button>

              <button
                type="button"
                className="choice-btn no-diagnosis"
                onClick={() => handleDiagnosisChoice(false)}
                onFocus={() => speak('  ')}
                aria-label=" "
              >
                <span className="choice-icon"></span>
                <span className="choice-text">,  </span>
                <span className="choice-desc">AI    </span>
              </button>
            </div>
          </div>

          <div className="info-box">
            <h4> </h4>
            <ul>
              <li><strong> :</strong> AI OCR  ,  ,    .</li>
              <li><strong> :</strong> AI        .</li>
              <li><strong> :</strong>    DB    .</li>
            </ul>
          </div>
        </div>
      </div>
    );
  }

  //   
  if (step === 'upload') {
    return (
      <div className="allergy-profile-setup">
        <div className="setup-card upload-card">
          <h2> </h2>
          <p className="setup-description">
               . AI  .
          </p>

          <div className="upload-area">
            <div className="upload-box">
              <input
                type="file"
                id="diagnosis-upload"
                accept="image/*"
                onChange={handleImageUpload}
                className="upload-input"
                aria-label=" "
              />
              <label htmlFor="diagnosis-upload" className="upload-label">
                <span className="upload-icon"></span>
                <span className="upload-text">
                  {diagnosisImage ? ' ' : '   '}
                </span>
                <span className="upload-hint">
                  JPG, PNG  
                </span>
              </label>
            </div>

            {diagnosisImage && (
              <div className="upload-preview">
                <p className="analyzing-text">
                  {ocrStatus === 'loading' && ' OCR   ...'}
                  {ocrStatus === 'recognizing' && `   ... ${ocrProgress}%`}
                  {ocrStatus === 'complete' && '  !'}
                  {ocrStatus === 'error' && '  '}
                </p>
                {ocrStatus === 'recognizing' && (
                  <div className="progress-bar-container">
                    <div className="progress-bar" style={{ width: `${ocrProgress}%` }}>
                      <span className="progress-text">{ocrProgress}%</span>
                    </div>
                  </div>
                )}
                {(ocrStatus === 'loading' || ocrStatus === 'recognizing') && (
                  <div className="loading-spinner"></div>
                )}
                {extractedText && (
                  <details className="extracted-text-details">
                    <summary>   ()</summary>
                    <pre className="extracted-text">{extractedText}</pre>
                  </details>
                )}
              </div>
            )}
          </div>

          <div className="ocr-info">
            <h4> Tesseract.js OCR </h4>
            <ul>
              <li><strong> :</strong>  +   </li>
              <li><strong> :</strong> OCR     </li>
              <li><strong>NLP  :</strong> 12     </li>
              <li><strong>  :</strong> ,     </li>
              <li><strong> :</strong>       </li>
              <li><strong> :</strong>      </li>
            </ul>
          </div>

          <button
            type="button"
            className="skip-btn"
            onClick={() => setStep('manual')}
            aria-label="  "
          >
               
          </button>
        </div>
      </div>
    );
  }

  //    ( )
  return (
    <div className="allergy-profile-setup">
      <div className="setup-card">
        <h2>  </h2>
        <p className="setup-description">
          {diagnosisImage
            ? 'OCR      .'
            : '        .'}
        </p>

        {diagnosisImage && (
          <div className="ocr-result-banner">
               !     .
          </div>
        )}

        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="name"> ()</label>
            <input
              type="text"
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder=""
              onFocus={() => speak(' ')}
            />
          </div>

          <div className="form-group">
            <label>  </label>
            <div className="allergen-grid">
              {commonAllergens.map(allergen => (
                <button
                  key={allergen}
                  type="button"
                  className={`allergen-chip ${selectedAllergens.includes(allergen) ? 'selected' : ''}`}
                  onClick={() => toggleAllergen(allergen)}
                  aria-pressed={selectedAllergens.includes(allergen)}
                  aria-label={`${allergen} ${selectedAllergens.includes(allergen) ? '' : ' '}`}
                >
                  {allergen}
                </button>
              ))}
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="custom-allergen"> </label>
            <div className="custom-input-group">
              <input
                type="text"
                id="custom-allergen"
                value={customAllergen}
                onChange={(e) => setCustomAllergen(e.target.value)}
                placeholder="  "
                onFocus={() => speak(' ')}
              />
              <button
                type="button"
                onClick={addCustomAllergen}
                className="add-btn"
                aria-label="  "
              >
                
              </button>
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="severity"> </label>
            <select
              id="severity"
              value={severity}
              onChange={(e) => {
                setSeverity(e.target.value);
                speak(e.target.options[e.target.selectedIndex].text);
              }}
              onFocus={() => speak('  ')}
            >
              <option value="high"> ( )</option>
              <option value="medium"> ( )</option>
              <option value="low"> ( )</option>
            </select>
          </div>

          {selectedAllergens.length > 0 && (
            <div className="selected-summary">
              <h3>   ({selectedAllergens.length})</h3>
              <div className="selected-tags">
                {selectedAllergens.map(allergen => (
                  <span key={allergen} className="selected-tag">
                    {allergen}
                    <button
                      type="button"
                      onClick={() => toggleAllergen(allergen)}
                      aria-label={`${allergen} `}
                      className="remove-tag"
                    >
                      
                    </button>
                  </span>
                ))}
              </div>
            </div>
          )}

          <button
            type="submit"
            className="submit-btn"
            aria-label="  "
            onFocus={() => speak('   ')}
          >
            <span></span>   
          </button>
        </form>
      </div>
    </div>
  );
}

export default AllergyProfileSetup;
